import Itemview from "./itemview";
import style from './kol.module.css'
function Kol(props) {
 
  return (
    <div className={style.kol}>
      {props.levels.map((i, j) => (
        <div className={style.itemkol} key={j}>
          <h1 style={{ color: "white" }}>{`Level${j+1}`}</h1>
          {i.map((i, j) => (
            <Itemview
              changelevel={props.changelevel}
              iteem={i}
              key={j}
               
            />
          ))}
        </div>
      ))}
 
    </div>
  );
}

export default Kol;
