import {
  leitner504datafail,
  leitner504datarequest,
  leitner504datasuccess,
  leitner504savechangedatafail,
  leitner504savechangedatarequest,
  leitner504savechangedatasuccess,
} from "./leitnerconstants";
import axios from "axios";
export const savechange = (userlogin,array) => {
 
  return async (dispatch) => {
    try {
      dispatch({ type: leitner504savechangedatarequest });
      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userlogin.userinfo.token}`,
        },
      };
      const { data } = await axios.put(
        "/api/levels/userlevels/",
        array,
        config
      );
    
      dispatch({ type: leitner504savechangedatasuccess, payload: data });      
    } catch (error) {
      dispatch({
        type: leitner504savechangedatafail,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };
};

export const getleitnerarray = (userlogin) => {
  return async (dispatch) => {
    try {
      dispatch({ type: leitner504datarequest });
      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userlogin.userinfo.token}`,
        },
      };
      const {data} = await axios.get(`/api/levels/userlevels/`, config);
      
      dispatch({ type: leitner504datasuccess, payload: data });
    } catch (error) {    
      dispatch({
        type: leitner504datafail,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };
};
 
