import React, { useEffect } from "react";
import { Route } from "react-router-dom";
import { Provider } from "react-redux";
import { useDispatch, useSelector } from "react-redux";
import store from "./reduxaffairs/store";
import Leitnerhome from "./leitnerhome";
const Lhome = (props) => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch({ type: "arraygir", payload: headerdata });
  }, [dispatch]);
  const userlogin = useSelector((state) => state.userlogin);

  const headerdata = [
    {
      array: [
        { to: "/leitner/", name: "All" },
        { to: "/leitner/page1", name: "Level 1" },
        { to: "/leitner/page2", name: "Level 2" },
        { to: "/leitner/page3", name: "Level 3" },
        { to: "/leitner/page4", name: "Level 4" },
        { to: "/leitner/page5", name: "Level 5" },
      ],
    },
    { className: "leitner" },
  ];

  return (
    <Provider store={store}>
      <Route
        render={() => (
          <Leitnerhome  userlogin={userlogin} />
        )}
        path="/leitner"
      />
    </Provider>
  );
};

export default Lhome;
