import {
  leitner504datafail,
  leitner504datarequest,
  leitner504datasuccess,
  leitner504savechangedatafail,
  leitner504savechangedatarequest,
  leitner504savechangedatasuccess,
} from "./leitnerconstants";

export const leitnerreducer = (state = {}, action) => {
  switch (action.type) {
    case leitner504datarequest:
      return { loading: true };
    case leitner504datasuccess:
      return { loading: false, data: action.payload };
    case leitner504datafail:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

export const leitnesavechangesrreducer = (state = {}, action) => {
  switch (action.type) {
    case leitner504savechangedatarequest:
      return { loading: true };
    case leitner504savechangedatasuccess:
      return { loading: false,success: true, data: action.payload };
    case leitner504savechangedatafail:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};
