import { jamlemahdoodkon } from "./components/a";
import React from "react";
import style from "./itemview.module.css";
import { useState } from "react";

const Itemview = (props) => {
  const [y, ygir] = useState(false);
  // y dar inja maani ast. agar true bood mani ra namayesh midahad va belaks

  return (
    <div className={style.itemview} onClick={props.clickkol}>
      <div className={style.overf} onClick={() => ygir((y) => !y)}>
        {y
          ?  '...'+jamlemahdoodkon(props.iteem.maani, 3)
          : jamlemahdoodkon(props.iteem.kalame, 3)+'...'}
      </div>
      <div className={style.jadokme}>
        {props.iteem.level !== 1 && (
          <button onClick={() => props.changelevel(props.iteem.id,false)}>بلد نیستم</button>
        )}
        {props.iteem.level !== 5 && (
          <button onClick={() => props.changelevel(props.iteem.id,true)}>بلدم</button>
        )}
        {props.description}
      </div>
    </div>
  );
};

export default Itemview;
